<template>
  <div class="product">
    <div class="leftTabs">
      <el-menu :default-active="active"  class="el-menu-vertical-demo"  @select="handleSelect">
      <el-menu-item index="1">
        <i class="el-icon-menu"></i>
        <span slot="title">定向诱骗</span>
      </el-menu-item>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title">固定诱骗</span>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-document"></i>
        <span slot="title">车载诱骗</span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-setting"></i>
        <span slot="title">系统化应用</span>
      </el-menu-item>
      <el-menu-item index="5">
        <i class="el-icon-setting"></i>
        <span slot="title">诱骗模块</span>
      </el-menu-item>
      <el-menu-item index="6">
        <i class="el-icon-setting"></i>
        <span slot="title">反无人机保障服务</span>
      </el-menu-item>
    </el-menu>
    </div>
    <div class="rightContent">
      <div v-if="active == '1'">
        <img :src="src1" />
      </div>
      <div v-else-if="active == '2'">
        <img :src="src2" />
      </div>
      <div v-else-if="active == '3'">
         <img :src="src3" />
      </div>
      <div v-else-if="active == '4'">
        <img :src="src4" />
      </div>
      <div v-else-if="active == '5'">
        <img :src="src5" />
      </div>
      <div v-else>
        <img :src="src6" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: '1',
      src1: require('../assets/img/product1.png'),
      src2: require('../assets/img/product2.png'),
      src3: require('../assets/img/product3.png'),
      src4: require('../assets/img/product4.png'),
      src5: require('../assets/img/product5.png'),
      src6: require('../assets/img/product6.png')
    }
  },
  watch: {
    $route(to){
      window.console.log(to)
      if (to.name == 'product') {
        this.active = to.params.id
      }
      
    }
  },
  mounted() {
    window.console.log(this.$route.params.id)
    this.active = this.$route.params.id
  },
  methods: {
    handleSelect(key) {
      this.$router.push({name:'product',params: {id:key}})
    },
  }
};
</script>

<style lang="scss" scoped>
.product{
  overflow: hidden;
  width: 100%;
  height: 100vh;
}
.leftTabs{
  float: left;
  width: 30%;
  ul{
    margin-left: 50%;
    width: 50%;
    text-align: left;
  }
  .el-menu {
    border: none !important;
  }
}
.rightContent{
  float: right;
  width: 70%;
  div {
    width: 100%;
    img{
      width: 100%;
      height: 100%;
    }
  }
}
</style>